import { interceptar } from './interceptador';

function hackChrome(controller) {
    interceptar(controller, 'alterarTipoDispositivoSelecionado', hackAlterarTipo);

    controller.registrarEventListener('keydown', event => hackInterceptarKeydown(event, controller));
    
    /* A partir do Chrome 62, comeou a ter problema no foco aps limpar o editor.
     * O problema foi evitado adicionando um \n aps o <br> ou substituindo o contedo
     * por "&nbsp;"" como feito no IE.
     */
    controller.limpar = function() {
        this._elemento.innerHTML = '<p data-tipo="artigo" style="min-height: 1em;"><br>\n</p>';
    };
}

/**
 * O chrome faz cache do counter-reset e, quando um counter-reset  introduzido
 * (por mudana de css), os elementos seguintes no so afetados.
 * 
 * @param {EditorArticulacaoController} ctrl 
 * @param {function} metodo
 * @param {Array} argumentos 
 */
function hackAlterarTipo(ctrl, metodo, argumentos) {
    let dispositivo = ctrl.contexto.cursor.dispositivo;
    let contadoresAntigos = extrairContadores(dispositivo);
    let retorno  = metodo.apply(ctrl, argumentos);
    let contadoresNovos = extrairContadores(dispositivo, contadoresAntigos.hash);

    /* Para cada contador novo, vamos redefinir o tipo dos prximos elementos,
     * at que se encontre em um elemento antigo o reincio do contador em questo.
     */
    for (let i = 0, proximo = ctrl.contexto.cursor.dispositivo.nextElementSibling; proximo && i < contadoresNovos.total; proximo = proximo.nextElementSibling) {
        let tipo = proximo.getAttribute('data-tipo');
        let contadoresProximo = extrairContadores(proximo);

        if (contadoresNovos.hash[tipo]) {
            redefinirContador(proximo);
        }

        // No h necessidade de reiniciar contadores quando o prximo j o reiniciava.
        for (let contador in contadoresProximo.hash) {
            if (contadoresNovos.hash[contador]) {
                contadoresNovos.hash[contador] = false;
                i++;
            }
        }
    }

    return retorno;
}

/**
 * Extrai os contadores reinciiados por este elemento.
 * 
 * @param {Element} dispositivo 
 * @param {Object} contadoresDesconsiderar Hash contendo os contadores que no devero ser considerados.
 */
function extrairContadores(dispositivo, contadoresDesconsiderar) {
    let counterReset = getComputedStyle(dispositivo).counterReset;
    let contadores = {};
    let cnt = 0;

    for (let regexp = /(.+?)\s\d+\s*/g, m = regexp.exec(counterReset); m; m = regexp.exec(counterReset)) {
        let contador = m[1];

        if (!contadoresDesconsiderar || !contadoresDesconsiderar[contador]) {
            contadores[contador] = true;
            cnt++;
        }
    }

    return { total: cnt, hash: contadores };
}

/**
 * Efetua a redefinio do contador.
 * 
 * @param {Element} dispositivo 
 */
function redefinirContador(dispositivo) {
    let tipo = dispositivo.getAttribute('data-tipo');
    dispositivo.removeAttribute('data-tipo');
    setTimeout(() => dispositivo.setAttribute('data-tipo', tipo));
}

/**
 * O Chrome tem um problema de desempenho que fica evidente ao importar
 * o LexML da Constituio Federal, selecionar todo o texto no editor
 * de articulao, e pressionar qualquer tecla que iria remover o texto
 * selecionado (seja por excluso ou substituio).
 * 
 * Para cada elemento no DOM a ser excludo ou substitudo, o navegador
 * processa os estilos do prximo elemento (conforme ferramenta de profile
 * do prprio navegador), tornando a operao O(n). Para contornar o problema,
 * realizamos a excluso do contedo via API, interceptando o keydown.
 * 
 * @param {KeyboardEvent} keyboardEvent 
 * @param {EditorArticulacaoController} editorCtrl 
 */
function hackInterceptarKeydown(keyboardEvent, editorCtrl) {
    /* Somente sero tratadas as alteraes de contedo. Portanto,
     * se houver qualquer tecla modificativa (ctrl, alt ou meta),
     * o evento ser ignorado. O evento s ser tratado se a tecla for
     * de contedo (letra, nmero ou enter), ou remoo (delete, backspace).
     */
    if (!keyboardEvent.ctrlKey && !keyboardEvent.altKey && !keyboardEvent.metaKey &&
        keyboardEvent.key.length === 1 || keyboardEvent.key === 'Delete' || keyboardEvent.key === 'Backspace' ||
        keyboardEvent.key === 'Enter') {

        let selection = editorCtrl.getSelection();
        let range = selection.getRangeAt(0);

        // Se no houver nada selecionado, ento no h problema de desempenho.
        if (!range.collapsed) {
            let inicio = range.startContainer.parentNode;
            let final = range.endContainer.parentNode;

            range.deleteContents();

            /* Se a tecla for de remoo, ento evitamos a ao padro.
             * Entretanto, se for de contedo, ento deixamos que a ao
             * padro seja executada, para que o novo contedo seja inserido
             * no lugar do contedo excludo.
             */
            if (keyboardEvent.key === 'Delete' || keyboardEvent.key === 'Backspace') {
                keyboardEvent.preventDefault();
            }

            /* O range da seleo, ainda que seja de todo o contedo, no abrange
             * o elemento inicial e o final. Isto , se o usurio selecionar tudo
             * usando Ctrl+A e prosseguir com a excluso com a tecla Delete, 
             * o contedo selecionado correpsonder ao primeiro n textual do primeiro
             * P at o ltimo n textual do tlimo P.
             * 
             *        <p><!-- incio da seleo -->n textual</p>
             *        <p>n textual intermedirio</p>
             *        <p>ltimo n textual<-- final da seleo --></p>
             * 
             * Assim, a excluso ir remover todos os ns textuais e os elementos intermedirios,
             * mantendo, entretanto, o primeiro e ltimo elemento (veja ilustrao acima,
             * em que a seleo foi demarcada em comentrio).
             * 
             * Para contornar esta situao, realizamos manualmente a excluso dos elementos,
             * se o elemento inicial da seleo for diferente do final, condicionando
             * a excluso  presena de contedo. Deve-se executar o procedimento somente
             * se o incio for diferente do final, pois a seleo pode ser apenas de
             * contedo intermedirio, como ilustrado a seguir:
             * 
             *      <p>este <!-- incio da seleo --> um <!-- final da seleo -->exemplo.</p>
             */
            if (inicio !== final) {
                if (inicio != editorCtrl._elemento && inicio.textContent.length === 0) {
                    inicio.remove();
                }

                if (final != editorCtrl._elemento && final.textContent.length === 0) {
                    final.remove();
                }

                /* Caso todo o contedo estivesse selecionado, o editor de articulao
                    * passar a ter nenhum elemento neste momento. Neste caso,
                    * deve-se recriar o contedo mnimo.
                    */
                if (editorCtrl._elemento.children.length === 0) {
                    editorCtrl.limpar();
                }
            }
            // ... mas se a seleo for todo o contedo de um nico elemento...
            else if (inicio.textContent.length === 0 && inicio.children.length === 0) {
                /* ento deve-se garantir o contedo mnimo, para que o cursor do pargrafo
                    * fique posicionado corretamente.
                    */
                inicio.innerHTML = '<br>';
            }
        }
    }
}

export default hackChrome;


