/**
 * Definio padro das opes do editor de articulao.
 */
export default {
    /**
     * Determina o escapamento de caracteres de cdigo alto unicode durante a exportao
     * para lexmlString.
     */
    escaparXML: false,
    
    /**
     * Determina o sufixo para os rtulos dos dispositivos.
     */
    rotulo: {
        separadorArtigo: ' \u2013',
        separadorArtigoSemOrdinal: ' \u2013',
        separadorParagrafo: ' \u2013',
        separadorParagrafoSemOrdinal: ' \u2013',
        separadorParagrafoUnico: ' \u2013',
        separadorInciso: ' \u2013',
        separadorAlinea: ')',
        separadorItem: ')'
    },

    /**
     * Determina se deve adotar o Shadow DOM, se suportado pelo navegador.
     */
    shadowDOM: false,

    /**
     * Determina se deve permitir a edio, ou se o componente ser somente para leitura.
     */
    somenteLeitura: false,

    /**
     * Determina se o editor de articulao deve aplicar transformao automtica.
     */
    transformacaoAutomatica: true,

    /**
     * Determina se deve validar o contedo atribudo ao componente.
     */
    validarAoAtribuir: true,

    /**
     * Determina as validaes que devem ocorrer.
     * Nenhuma validao ocorre se o editor for somente para leitura.
     */
    validacao: {
        /**
         * Determina se deve validar o uso de caixa alta.
         */
        caixaAlta: true,

        /**
         * Determina se deve validar o uso de aspas em citaes.
         */
        citacao: true,

        /**
         * Determina se deve validar a presena de mltiplos elementos em uma enumerao.
         */
        enumeracaoElementos: true,

        /**
         * Determina se deve validar o uso de letra maiscula no caput do artigo e em pargrafos.
         */
        inicialMaiuscula: true,

        /**
         * Determina se deve validar as pontuaes.
         */
        pontuacao: true,

        /**
         * Determina se deve validar pontuao de enumerao.
         */
        pontuacaoEnumeracao: true,

        /**
         * Determina se deve exigir sentena nica no dispositivo.
         */
        sentencaUnica: true
    }
};


